.App {
  text-align: center;
  color: #ffffff;
  background-color: #282c34;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
  pointer-events: none;
}

.image-gallery-slide, ::after, ::before {
  background-color: #282c34;
}

.image-gallery-slide img {
  width: 600px;
  height: 600px;
}

.form-control {
  width: 15%;
  margin-left: auto;
  margin-right: auto;
}

.App-header {
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: #282c34;
}

.css-42igfv {
  color: #ffffff;
}

.css-50etie svg {
  background-color: transparent;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
